body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.c-card {
  max-width: 20rem;
  width: 100%;
  margin: 0 auto;
  position: relative;
  border-radius: 10px;
}

.c-card:nth-child(n+2) {
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .c-card:nth-child(n+2) {
    margin-top: initial;
    margin-left: 1.25rem;
  }
}

.c-card__border {
  position: absolute;
  top: 0;
  left: 0;
  width: 3.125rem;
  height: 100%;
  z-index: 1;
  border-radius: 10px 0 0 10px;
}

.c-card__img {
  position: relative;
  z-index: 3;
}

.c-card__img::before {
  content: "";
  display: block;
  padding-top: 62.5%;
}

.c-card__img img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.c-card__img img {
  border-radius: 10px 10px 0 0;
}

.c-card__body {
  position: relative;
  background-color: #F7F7F7;
  border-radius: 0px 0px 10px 10px;
}

.c-card__title {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 700;
  font-size: 1.25rem;
  color: #AF9E97;
}

.c-card__text {
  font-family: "Noto Sans JP", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  color: #000;
  margin-top: 0.625rem;
}

.swiper-slide {
  margin-left: 0.9375rem;
}